::ng-deep {
  .owl-dt-container {
    background: var(--app-color-white);
    color: var(--app-color-dark);
    * {
      // background: var(--app-color-white);
      color: var(--app-color-dark) !important;
    }

    .owl-dt-calendar-cell-selected {
      border: 1px solid var(--app-color-dark) !important;
      background-color: var(--app-color-dark) !important;
      color: var(--app-color-white) !important;
    }

    .owl-dt-calendar-cell-today {
      border: 1px solid var(--app-color-dark) !important;
    }
  }
}
